$dark-color:  #5a1d1af5;
$dark-transparent: rgba(90, 29, 26, 0.88);
$light-color: #eee6e6;
$light-transparent: #eee6e691;

@mixin onMobile {
  @media (max-width: 1024px) { @content }
}

.library {
  margin: 10px auto;
  min-width: 44vmin;
  width: fit-content;
  background-color: $dark-transparent;
  border: 1px solid transparent;
  border-radius: 50px;
  overflow: hidden;

  &__element {
    margin-bottom: 6vh;
    padding: 2vh 8vmin;
    background-color: $dark-transparent;
    list-style-type: none;

    &:first-child {
      margin-top: 6vh;
    }
  }

  @include onMobile {
    width: 92%;
  }
}

.breadcrumbs {
  background-color: $dark-transparent;

  &__container {
    margin: auto;
    padding: 1vmin 2vmin 1vmin 14vmin;
    display: flex;
    align-items: center;

    @include onMobile {
      padding: 10px 0 10px 6px;
      width: fit-content;
      flex-direction: column;
      align-items: flex-start;
      gap: 8px;
    }
  }

  &__container * {
    font-size: 16px;
    line-height: 16px;
    color: $light-color;
    text-decoration: none;
    cursor: pointer;

    @include onMobile {
      font-size: 12px;
    }
  }
}

.itemLink {
  display: block;
  font-size: 24px;
  color: $light-color;
  text-decoration: none;
}

.title {
  margin: 5vh 0;
  padding: 15px;
  text-align: center;

  &--small {
    margin: 0;
  }

  @include onMobile {
    font-size: 24px;
  }
}

.download-link {
  width: fit-content;
  margin: 5vh auto;
  display: flex;
  font-size: 20px;
  background-color: transparent;

  &--small {
    margin: 5vh auto;
  }

  @include onMobile {
    margin: 5vh auto 8vh;
    font-size: 18px;
  }
}

.player {
  margin: 5vh auto;
  display: flex;
  background-color: transparent;

  &--small {
    margin: 5vh auto;
  }
}

