@font-face {
  src: url(../src/fonts/PoetsenOne-Regular.ttf);
  font-family: alm;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: alm, sans-serif;
  color: #eee6e6;
}

html {
  padding-bottom: 120px;
  min-width: 375px;
  background-image: url(./backgrounds/GdanskStarowka.jpg);
  background-size: cover;
  background-attachment: fixed;
}

#root {
  overflow: hidden;
}
